import moment from 'moment'
import { getFilters } from "./filters"
import { sortNotes, removeNote, getNotes, saveNotes} from "./notes"
//Generate the DOM Structure for a note

const generateNoteDOM = (note) => {
    
    const noteEl = document.createElement("div")
    const textEl = document.createElement("a")
    const button = document.createElement("button")
    //setup the remove note button
    button.textContent = "x"
    noteEl.appendChild(button)
    button.addEventListener('click',() => {
        removeNote(note.id)
        saveNotes(notes)
        renderNotes()
    })

    //setup the note title text
    note.title.length > 0 ? textEl.textContent = note.title : textEl.textContent = "unnamed note"
    
    textEl.setAttribute("href",`/edit.html#${note.id}`)
    noteEl.appendChild(textEl)
    return noteEl
}

//Render application notes

const renderNotes = ()=>{
    const notesEl = document.querySelector("#notes")
    const filters = getFilters()
    const notes = sortNotes(filters.sortBy)
    const filteredNotes = notes.filter((note) => note.title.toLowerCase().includes(filters.searchText.toLowerCase()))

    notesEl.innerHTML =""
    if (filteredNotes.length > 0) {
        filteredNotes.forEach((note) => {
            const noteEl = generateNoteDOM(note)
            notesEl.appendChild(noteEl)
          })
    }else {
        const emptyMessage = document.createElement ('p')
        emptyMessage.textContent = "No notes to show"
        notesEl.appendChild(emptyMessage)
    }
    
}
const initializeEditPage = (noteID) => {
    const titleElement =document.querySelector("#note-title")
    const bodyElement =document.querySelector("#note-body")
    const dateElement =document.querySelector("#last-edit")
    const notes = getNotes()
    const note = notes.find((note)=> note.id === noteID)

    if (!note) {
     location.assign('/index.html')
    } 

titleElement.value = note.title
bodyElement.value = note.body
dateElement.textContent = generateLastEdited(note.updatedAt)  

}
//generate time stamp 
const generateLastEdited = (timestamp) => `Last Updated: ${moment(timestamp).fromNow()}`

export {generateNoteDOM, renderNotes, generateLastEdited, initializeEditPage}